@import "../../node_modules/bootstrap/scss/bootstrap-reboot.scss";
@import "../../node_modules/bootstrap/scss/bootstrap-grid.scss";

$menu-margin: 45px;

* {
    box-sizing: border-box; }


body {
    font-family: TT Norms, sans-serif;
    font-size: 18px;
    color: #111; }



ul {
    padding: 0;
    margin: 0;
    list-style: none; }

.nav-btn {
    padding: 23px 25.5px;
    line-height: 0;
    border: 2px solid #54B125;
    border-radius: 50px;
    font-size: 14px;
    background: transparent;
    transition: .3s;
    &:hover, &:active, &:focus {
        outline: 0;
        background-color: #54B125;
        color: #fff;
        transition: .3s; } }

.section-title {
    padding: 0 15px;
    text-align: center;
    font-weight: bold;
    font-size: 32px;
    line-height: 38px;
    text-transform: uppercase;
    margin-bottom: 12px; }


.section-short-desc {
    line-height: 21px;
    text-align: center;
    padding: 0 15px; }

.icon-bread {
    width: 6px;
    height: 10px;
    margin: 0 12px; }

.bread-link {
    font-size: 12px;
    line-height: 14px;
    color: #A5A5A5; }

a.bread-link {
    transition: .3s;
    &:hover, &:active, &:focus {
        color: #54B125;
        text-decoration: none;
        transition: .3s; } }

span.bread-link {
    color: #111; }

.bread-crumbs {
    margin: 30px auto; }

.single-page-title {
    font-weight: bold;
    font-size: 36px;
    line-height: 42px;
    margin-bottom: 30px; }

.mobile-logo {
    position: relative;
    background: none;
    .container {
        position: relative; } }


#burger {
    display: none;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 15px;
    width: 28px;
    height: 18px;
    cursor: pointer;
    &:before, &:after, span {
        content: '';
        position: absolute;
        height: 2px;
        background: #111;
        border-radius: 2px;
        right: 0; }
    &:before {
        width: 100%;
        top: 0;
        transition: .3s; }
    &:after {
        width: 24px;
        bottom: 0;
        transition: .3s; }
    span {
        width: 20px;
        top: 50%;
        transform: translateY(-50%);
        transition: .3s; }
    &.active {
        &:before {
            width: 100%;
            top: calc(50% - 1px);
            transform: rotate(-45deg);
            transition: .3s; }
        &::after {
            width: 100%;
            bottom: calc(50% - 1px);
            transform: rotate(45deg);
            transition: .3s; }
        span {
            transform: scale(0);
            transition: .3s; } } }

