@media (max-width: 1559px) {
    [class*="decoration-"] {
        display: none; } }

@media (max-width: 1199px) {
    #burger {
        display: block; }

    .logo-container {
        padding: 0;
        max-width: 100%; }

    .mobile-logo {
        padding: 30px 0;
        z-index: 15;
        transition: .2s;
        &.active {
            background-color: #fff;
            box-shadow: none; }
        &.single-page-menu:not(.active),
        &.scrolled:not(.active) {
            background: #FFFFFF;
            box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.05); } }
    .logo {
        width: 30px;
        height: 45px; }

    .logo-desc {
        font-weight: 500;
        font-size: 12px;
        line-height: normal;
        color: #111; }

    .main-menu {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 120px;
        z-index: 99;
        overflow: hidden;
        &.active {
            height: 100%;
            overflow: auto; } }


    .menu-wrap {
        position: absolute;
        padding-top: 105px;
        width: 100%;
        display: flex;
        flex-direction: column-reverse;
        top: 0;
        transform: translateY(-100%);
        transition: .4s;
        min-height: 100%;
        background-color: #fff;
        z-index: 10;
        justify-content: space-between;
        align-items: center;
        padding-bottom: 40px;
        &.active {
            top: 0;
            transform: translateY(0); } }

    .top-contacts {
        background: none;
        height: auto; }

    .contact-row {
        display: block; }

    .top-left {
        flex-direction: column;
        flex: 1;
        width: 100%;
        max-width: 100%; }

    .top-right {
        width: 100%;
        max-width: 100%;
        margin: 0;
        justify-content: center;
        .top-col {
            margin-right: 10px;
            margin-left: 10px; } }

    .top-col {
        color: #111;
        padding-bottom: 30px;
        a {
            color: #111;
            &:hover {
                color: #111; } } }

    .nav-container {
        padding-top: 20px;
        padding-bottom: 25px; }

    .nav-wrap {
        width: 100%;
        max-width: 100%;
        flex: 1; }

    .nav-items {
        flex-direction: column;
        text-align: center;
        width: 100%; }

    .nav-item, .nav-item:nth-last-child(1) {
        margin: 0;
        &:after {
            display: none; } }

    .nav-item {
        padding-bottom: 15px;
        a {
            font-weight: 500;
            font-size: 14px;
            line-height: 19px; } }

    .nav-btn {
        color: #fff;
        background: #54B125;
        width: 300px;
        align-self: center; }

    .lock {
        overflow: hidden; }

    // Конец адаптации меню

    .bread-crumbs {
        margin: 140px auto 30px; }

    .first-section-title {
        font-size: 30px;
        line-height: normal; }

    .first-section-desc {
        max-width: 60%;
        font-size: 17px;
        line-height: normal; }

    .header-img {
        width: 400px;
        right: -30px; }

    .first-section-more {
        width: 250px;
        font-size: 16px;
        padding: 30px 55px; }

    .more-arrow {
        width: 50px;
        height: 50px; }

    .first-section-container {
        padding: 200px 15px; }

    .advantage-desc {
        font-size: 14px;
        width: 150px; }

    .advantage-container {
        width: 100px;
        height: 100px; }

    .icon-advantage {
        height: 50px;
        width: 50px; }

    .section-title {
        font-size: 28px;
        line-height: normal; }

    .section-short-desc {
        font-size: 16px;
        line-height: normal; }

    .calc-btn {
        padding: 30px 35px; }

    .catalog-plus {
        font-size: 100px;
        margin: 120px 0 90px; }

    .product-link {
        font-size: 14px; }

    .catalog-desc {
        font-size: 14px; }

    .footer-wrap {
        flex-direction: column; }

    .footer-contact-container {
        width: 100%; }

    .footer-title {
        max-width: 100%; }

    .footer-container {
        margin: auto;
        padding-right: 15px; }

    .footer-map {
        height: 400px;
        width: 100%; }

    .footer-title {
        font-size: 30px;
        line-height: normal; }

    .footer-info .logo-container {
        flex-grow: unset;
        max-width: 235px; }

    p.footer-work-time {
        margin: 0;
        font-size: 12px; }

    .footer-social a {
        width: 40px;
        height: 40px;
        margin: 0 7px; }

    .footer-social-icon {
        width: 13px;
        height: 13px; } }

@media (max-width: 991px) {
    .first-section-container {
        padding: 180px 15px 130px; }

    .first-section-title {
        font-size: 28px; }

    .first-section-desc {
        max-width: 65%;
        font-size: 15px; }

    .header-img {
        width: 340px; }

    .first-section-more {
        width: 220px;
        font-size: 14px;
        padding: 25px 40px; }

    .more-arrow {
        width: 40px;
        height: 40px; }

    .icon-btn-arrow {
        width: 12px;
        height: 12px; }

    .advantages {
        justify-content: center;
        flex-wrap: wrap; }

    .advantage {
        width: 33.33%;
        margin-bottom: 30px; }

    .advantages-section {
        padding: 90px 0 45px; }

    .product {
        width: 33.33%; }

    .build-img {
        width: 33.33%; }

    .calc-btn {
        width: 250px;
        font-size: 14px; }

    .section-title {
        font-size: 26px; }

    .footer-title {
        font-size: 28px; }

    .footer-contact a {
        font-size: 16px; }

    .footer-work-time {
        display: none; } }

@media (max-width: 767px) {
    .first-section-container {
        overflow: visible; }

    .header-img {
        bottom: 0;
        top: auto;
        right: 50%;
        transform: translate(50%, 35%); }

    .first-section-title,
    .first-section-desc {
        max-width: 100%; }

    .first-section-container {
        padding: 180px 15px 210px; }

    .advantages-section {
        padding: 150px 0 45px; }

    .product {
        width: 50%; } }

@media (max-width: 575px) {
    .advantage {
        width: 50%; }

    .top-right {
        flex-wrap: wrap; }

    .footer-phones {
        margin-bottom: 30px; }

    .footer-contacts {
        flex-direction: column; }

    .footer-contact-container {
        padding: 60px 0 50px; }

    .footer-submit-wrap {
        flex-direction: column; }

    .footer-btn {
        width: 100%; }

    .confrim-text {
        margin: 20px 0 0;
        max-width: 100%; }

    .footer-social a {
        width: 30px;
        height: 30px;
        margin: 0 3px; }

    .footer-social-icon {
        width: 10px;
        height: 10px; }

    .footer-info {
        .logo-desc {
            font-size: 10px;
            margin-right: 20px; }
        .logo {
            width: 30px;
            height: 30px; } } }

@media (max-width: 425px) {
    .logo-desc {
        font-size: 10px;
        margin-right: 20px; }
    .logo {
        width: 30px;
        height: 30px; }

    .first-section-desc {
        font-size: 14px;
        margin-bottom: 50px; }

    .first-section-title {
        font-size: 18px;
        margin-bottom: 20px; }

    .header-img {
        width: 300px;
        transform: translate(53%, 35%); }

    .footer-info .logo-desc {
        font-size: 7px; }

    .footer-info {
        padding: 20px 15px; }

    .footer-title {
        font-size: 24px; }

    .section-title {
        font-size: 20px; }

    .section-short-desc {
        font-size: 14px; }

    .calc-btn {
        width: 100%;
        font-size: 16px; }

    .build-imgv {
        width: 50%; }

    .builds {
        margin: 30px 0 40px; }

    .build-img {
        width: 50%; }

    .build-section {
        padding-bottom: 70px; }

    .advantages-section {
        padding-bottom: 40px; }

    .advantage-container {
        margin: 0; }

    .advantage-desc {
        margin: 0 0 0 20px;
        text-align: left; }

    .advantage {
        width: 100%;
        flex-direction: row;
        margin-bottom: 25px; }

    .top-right {
        justify-content: flex-start;
        margin-bottom: 20px;
        .top-col {
            margin: 0;
            padding-bottom: 10px;
            &:nth-child(odd) {
                margin-right: 20px; } } }

    .nav-btn {
        width: calc(100% - 30px); }

    .top-left .top-col {
        width: 100%;
        align-items: flex-start;
        .top-contacts-icons {
            margin-top: 4px; } }

    .products {
        margin: 30px -10px 50px; }

    .product {
        padding: 0 10px;
        margin-bottom: 20px; }

    .product-link {
        font-size: 9px;
        height: 50px; }

    .catalog-desc {
        font-size: 9px; }

    .catalog-plus {
        margin: 65px 0; } }
