.icon-close {
    stroke: #111;
    width: 16px;
    height: 16px;
    position: absolute;
    top: 40px;
    right: 35px;
    cursor: pointer;
    transition: .3s;
    &:hover, &:focus, &:active {
        stroke: #54B125;
        transition: .3s; } }

.modal {
    width: 500px !important;
    max-width: calc(100% - 20px) !important;
    max-height: calc(100% - 20px) !important; }

.feedback-content {
    position: relative;
    padding: 55px 65px 30px; }

.modal-title {
    font-weight: 500;
    font-size: 28px;
    line-height: 33px;
    margin-bottom: 40px; }

.feedback-submit {
    box-shadow: 0px 15px 25px rgba(84, 177, 37, 0.2);
    margin-bottom: 20px;
    width: 100%;
    margin-top: 10px; }

.modal-input {
    padding: 15px 25px;
    color: #111;
    background: #F4F4F4;
    border-radius: 50px;
    margin-bottom: 20px;
    border: 1px solid #F4F4F4;
    width: 100%;
    font-size: 16px;
    line-height: 19px;
    color: #111;
    transition: border-color .2s;
    &:focus, &:active, &:hover {
        outline: 0;
        border: 1px solid #54B125;
        transition: border-color .2s; } }

.modal-policy {
    font-size: 12px;
    line-height: 14px;
    color: #747474;
    a {
        color: #747474;
        font-weight: 500;
        &:hover, &:focus, &:active {
            color: #747474;
            text-decoration: none; } } }

.thank-content {
    position: relative;
    padding: 65px 65px 55px;
    text-align: center; }

.icon-thank {
    width: 140px;
    height: 140px;
    margin: 0 0 35px; }

.thank-title {
    margin-bottom: 20px; }

.thank-text {
    font-size: 18px;
    line-height: 24px;
    color: #2E2E2E;
    margin: 0; }
